.container{display:flex}.container .main{height:calc(100vh - 9rem);display:flex;flex-direction:column;justify-content:center;width:60rem;margin-top:-4rem;margin-left:37.5%;transition:transform .3s ease}.container .main .profile{display:flex;align-items:center;width:100%}.container .main .profile .pfpContainer{position:relative}.container .main .profile .pfpContainer img{width:12rem;aspect-ratio:1;object-fit:cover;border-radius:50%;cursor:pointer;image-rendering:-webkit-optimize-contrast}.container .main .profile .userInfo{margin-left:2.2rem;display:flex;flex-direction:column;flex:auto}.container .main .profile .userInfo .usernameAndEdit{display:flex;align-items:center}.container .main .profile .userInfo .usernameAndEdit .username{color:#24292f;font-size:2.6rem;font-weight:400}.container .main .profile .userInfo .usernameAndEdit .edit{margin-bottom:.8rem;margin-left:1rem;width:2rem;cursor:pointer}.container .main .profile .userInfo .location{margin-top:.3rem;font-size:1rem;color:#24292f;font-weight:400}.container .main .profile .userInfo .bio{margin-top:.5rem;font-size:1.4rem;font-style:italic;font-weight:300;color:#757575}